.progress-meter {
  position: relative; }

.progress-bar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  width: 0;
  -webkit-transition: width 125ms ease-out 500ms;
  -o-transition: width 125ms ease-out 500ms;
  transition: width 125ms ease-out 500ms; }

.progress-meter__label {
  position: relative;
  display: block;
  z-index: 100; }

.progress-meter.fait {
  background-color: rgba(63, 227, 181, 0.1);
  color: #50a384; }
  .progress-meter.fait .progress-bar {
    background-color: #2bca9e; }

.progress-meter.en-cours {
  background-color: rgba(255, 105, 85, 0.1);
  color: #d35d4a; }
  .progress-meter.en-cours .progress-bar {
    background-color: #ff6955; }

.progress-meter.venir {
  background-color: rgba(191, 203, 211, 0.1);
  color: #5b7588; }
  .progress-meter.venir .progress-bar {
    background-color: #bfcbd3; }

.progress-meter.timeline {
  background-color: #e0e0e0; }
  .progress-meter.timeline .progress-bar {
    background-color: #7181ff; }



